import cityData from "../../common/js/cityData"
import Scroll from "../../components/scroll/scroll"
import Alphabet from '../../components/Alphabet/Alphabet'
export default {
  data () {
    return {
      currView: '',
      visitCityList: [
        {zip: "010", name: "北京"},
        {zip: "021", name: "上海"},
      ],
      hotCityList: [
        {zip: "010", name: "北京"},
        {zip: "021", name: "上海"},
        {zip: "020", name: "广州"},
        {zip: "0755", name: "深圳"},
        {zip: "022", name: "天津"},
        {zip: "028", name: "成都"},
        {zip: "0571", name: "杭州"},
        {zip: "023", name: "重庆"},
        {zip: "025", name: "南京"},
      ],
      cityList: [],
      selectCity: {},
    }
  },
  components:{Scroll,Alphabet},
  created() {
    setTimeout(()=>{
      this.initCityList()
    },1000)
    this.currView = ''
  },

  methods: {
    //初始化城市列表
    initCityList() {
      this.cityList = cityData
    },

    //点击城市名，派发点击事件给父组件
    clickCity(city) {
      this.$emit('cityService', city.name)
    }
  },
}



