
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import axios from 'axios'
export default {
name: "gift-paper",
data(){
return{
scanImg:false,
qrcodeImgUrl:"",
allPaper:[],
activityId:"",
unUsePaper:[
"https://s1.ax1x.com/2018/07/28/PUdXy8.png",
],
paperIndex:0
}
},
created(){
this.getData()
},

methods:{
showCode(item){
this.$router.push({
path: `/game`
})
},
getData(){
axios.get("http://192.168.0.55:8899/customerLoginController.do?method=getMyGameCoupon&page=1&rows=5&loginType=2").then((res)=>{
console.log(res)
this.allPaper=res.data.dataList;
this.activeId=res.data.dataList[0].activityId;
this.$store.commit('updateActivityId',  this.activeId)
})
},

back() {
this.$router.go(-1);
},
}
}



