
import Bmap from 'BMap'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import Scroll from "../../components/scroll/scroll"
import Slider from '../../components/slider/slider'
import Tab from '../../components/public/navbar'
import Loading from '../../components/loading/loading'
import axios from 'axios'
import CityData from '../../common/js/cityData'

export default {
name: "home2",
components:{Scroll,Slider,Tab,Loading,Swiper, SwiperItem},
data(){
return{
swiperItemIndex: 0,
pullup: true,
hasMore: true,
loading: false,
code:"",
firstCode:"",
activityData: [],
city:"",
isFirst:0,
location:{},
firstCity:"",
cityData:[],
iconMap:[],
imgUrls: [
'http://gw.alicdn.com/imgextra/i4/164/TB2RhntAFGWBuNjy0FbXXb4sXXa_!!164-0-luban.jpg',
'http://img.alicdn.com/imgextra/i2/52/TB2IYJEjyMnBKNjSZFzXXc_qVXa_!!52-0-luban.jpg',
'http://gw.alicdn.com/imgextra/i1/127/TB2YC6RjrZnBKNjSZFKXXcGOVXa_!!127-0-lubanu.jpg',
'http://gw.alicdn.com/imgextra/i4/87/TB27UR8i5MnBKNjSZFCXXX0KFXa_!!87-0-lubanu.jpg',
'http://gw.alicdn.com/imgextra/i2/190/TB2mLdhA29TBuNjy0FcXXbeiFXa_!!190-0-lubanu.jpg',
'http://img.alicdn.com/imgextra/i2/52/TB2IYJEjyMnBKNjSZFzXXc_qVXa_!!52-0-luban.jpg',
'http://gw.alicdn.com/imgextra/i1/127/TB2YC6RjrZnBKNjSZFKXXcGOVXa_!!127-0-lubanu.jpg',
'http://gw.alicdn.com/imgextra/i4/87/TB27UR8i5MnBKNjSZFCXXX0KFXa_!!87-0-lubanu.jpg',
'http://gw.alicdn.com/imgextra/i2/190/TB2mLdhA29TBuNjy0FcXXbeiFXa_!!190-0-lubanu.jpg'
],
}
},
computed: {
curCity () {
return this.$store.state.curCity;
},
},
mounted(){
this.$nextTick(()=>{
this.$refs.scroll.refresh();
})
},
created(){
this.code=this.$store.state.code;
this.isFirst=this.$store.state.isFirst;
console.log(this.code)
this.$nextTick(()=>{
this.$refs.scroll.refresh();
this.cityData=CityData;
})
if (this.$store.state.isLogin) {
if(this.isFirst===0){
console.log("wocao1")
this.getLocation();
}
else {
console.log("wocao2")
this.getActivityData(this.code);
}
this.getCategoryData();
this.city=this.$store.state.c
}

},
methods:{
getLocation () {
const geolocation = new BMap.Geolocation()
geolocation.getCurrentPosition((r) => {
if (r.point) {

var myGeo = new BMap.Geocoder();

myGeo.getLocation(new BMap.Point(r.longitude,r.latitude), (res)=>{
if (res){
this.$store.commit('updateFirst',1);
this.firstCity=res.addressComponents.city;
for (let i=0;i<this.cityData.length;i++){
for (let j=0;j<this.cityData[i].list.length;j++){
if(this.firstCity===this.cityData[i].list[j].name){
this.firstCode=this.cityData[i].list[j].zip
break;
}
}
break;
}
this.getActivityData(this.firstCode);
}
});
}
}, { enableHighAccuracy: true })
},
selectCity(){
this.$router.push({
path: `/selectcity`
})
this.$destroy();
},

search() {
this.$router.push({
path: `/search`
})
},
loadImage() {



if (!this.checkloaded) {
this.checkloaded = true
this.$refs.scroll.refresh();
}
},
searchMore() {
if (this.activityData.length > 15) {
this.$refs.loading.style.display = "flex"
this.loading = true;
setTimeout(() => {
let last = this.activityData[this.activityData.length - 1];
for (let i = 1; i <= 10; i++) {
this.activityData.push(last + i);
}
this.$refs.loading.style.display = "none"
this.loading = false;
}, 2500);
}
else {
this.$refs.loading.style.display = "none"
this.loading = false;
}
},
getDate(time) {
const date = new Date(time);
const Y = date.getFullYear() + '-';
const M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
const D = date.getDate() + ' ';
const h = date.getHours() + ':';
const m = date.getMinutes() + ':';
const s = date.getSeconds();
return Y + M + D + h + m + s;
},
getCategoryData() {
axios.get("http://192.168.0.55:8899/industryCategoryController.do?method=getIndustryCategorys&loginType=2").then((res) => {

this.iconMap = res.data.dataList;
})
},
getActivityData(code) {
const url="http://192.168.0.55:8899/customerLoginController.do?method=getActivitys&page=1&rows=1&loginType=2&shiCode="+code
console.log(url);
axios.get(url).then((res) => {

console.log(res.data);
this.activityData = res.data.dataList;
})
},

selectItem(item) {
this.$router.push({
path: `/activityshoplist/${item.activityId}`
})
},
selectCgtegory(item) {
this.$router.push({
path: `/categoryshoplist/${item.industryCategoryId}`
})
},
}
}



