
import $ from 'jquery'
import wx from 'weixin-js-sdk'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import axios from 'axios'
export default {
name: "game",
components:{Alert,TransferDom},
data() {
return {
scanImg:false,
show:false,
flag: false,
Logo:[],
ColorLogo:[],
gameId:"",
merchants:[],
code_num:[],
url3:"",
activityId:""
}
},
created() {
this.activityId=this.$store.state.activityId
axios.get("http://192.168.0.55:8899/activityGameController.do?method=getGames&activityId="+this.activityId+"&loginType=2").then((res)=>{
console.log(res.data)
this.gameId=res.data.data.gameId
this.merchants=res.data.data.merchants;
axios.get("http://192.168.0.55:8899/activityGameController.do?method=getMerchantsLogo&loginType=2&gameId="+this.gameId).then((res)=>{
console.log(res.data.dataList)
this.getImage(res.data.dataList)
this.getCodeNum(this.merchants)
this.url3="http://192.168.0.55:8899/activityGameController.do?method=getCustomerGameInfo&gameId="+this.gameId+"&loginType=2"
axios.get(this.url3).then((res)=>{
this.setGameState(res.data.dataList);

})
})
})
console.log(location.href.split('#')[0]);
},
mounted() {
this.play();

},
methods: {
showOrhide(){
this.scanImg=!this.scanImg;
},
setGameState(data){
const paixu=data[0].paixu;
console.log(paixu);
const boat=$('.boat')
if(paixu===1){
boat.css({"bottom":" 68%","left":"52%"})
$('.brand-position0').children("img").attr("src",this.ColorLogo[0])
$('.brand-position0').children("img").attr("disabled",false)
}else if(paixu===2){
boat.css({"bottom":" 55%","left":"29%"})
$('.brand-position1').children("img").attr("src",this.ColorLogo[1])
$('.brand-position0').children("img").attr("src",this.ColorLogo[0])
$('.brand-position0').children("img").attr("disabled",false)
$('.brand-position1').children("img").attr("disabled",false)
}else if(paixu===3){
boat.css({"bottom":" 38%","left":"32%"})
$('.brand-position3').children("img").attr("src",this.ColorLogo[2])
$('.brand-position1').children("img").attr("src",this.ColorLogo[1])
$('.brand-position0').children("img").attr("src",this.ColorLogo[0])
}else if(paixu===4){
boat.css({"bottom":" 15%","left":"70%"})
$('.brand-position4').children("img").attr("src",this.ColorLogo[3])
$('.brand-position3').children("img").attr("src",this.ColorLogo[2])
$('.brand-position1').children("img").attr("src",this.ColorLogo[1])
$('.brand-position0').children("img").attr("src",this.ColorLogo[0])
}
else if(paixu===5){
boat.css({"bottom":" 10%","left":"50%"})
$('.brand-position6').children("img").attr("src",this.ColorLogo[4])
$('.brand-position4').children("img").attr("src",this.ColorLogo[3])
$('.brand-position3').children("img").attr("src",this.ColorLogo[2])
$('.brand-position1').children("img").attr("src",this.ColorLogo[1])
$('.brand-position0').children("img").attr("src",this.ColorLogo[0])
}
},
getImage(data){
for(let i=0;i<data.length;i++){
this.Logo.push(data[i].merchantLogo+"&loginType=2")
this.ColorLogo.push(data[i].merchantColorLogo+"&loginType=2")
}
},
getCodeNum(data){
for(let i=0;i<data.length;i++){
this.code_num.push(data[i].merchantGameCode+data[i].merchantGameChcekCode)
}
},


































clickCode(index) {
const boat=$('.boat')
if(index===4){
axios.get("http://192.168.0.55:8899/activityGameController.do?method=saveActicityGameMerchantInfo&code_num="+this.code_num[4]+"&loginType=2").then((res)=>{
axios.get(this.url3).then((res)=>{
console.log(res.data);
boat.css({"bottom":" 10%","left":"50%"})
$('.brand-position6').children("img").attr("src",this.ColorLogo[4])
this.showOrhide();
})
})

}
else if(index===3){
axios.get("http://192.168.0.55:8899/activityGameController.do?method=saveActicityGameMerchantInfo&code_num="+this.code_num[3]+"&loginType=2").then((res)=>{
axios.get(this.url3).then((res)=>{
console.log(res.data);
boat.css({"bottom":" 15%","left":"70%"})
$('.brand-position4').children("img").attr("src",this.ColorLogo[3])
})
})
}
else if(index===2){
axios.get("http://192.168.0.55:8899/activityGameController.do?method=saveActicityGameMerchantInfo&code_num="+this.code_num[2]+"&loginType=2").then((res)=>{
axios.get(this.url3).then((res)=>{
console.log(res.data);
boat.css({"bottom":" 38%","left":"32%"})
$('.brand-position3').children("img").attr("src",this.ColorLogo[2])
})
})
}
else if(index===1){
axios.get("http://192.168.0.55:8899/activityGameController.do?method=saveActicityGameMerchantInfo&code_num="+this.code_num[1]+"&loginType=2").then((res)=>{
axios.get(this.url3).then((res)=>{
console.log(res.data);
boat.css({"bottom":" 55%","left":"29%"})
$('.brand-position1').children("img").attr("src",this.ColorLogo[1])
})
})
}
else if(index===0){
axios.get("http://192.168.0.55:8899/activityGameController.do?method=saveActicityGameMerchantInfo&code_num="+this.code_num[0]+"&loginType=2").then((res)=>{
axios.get(this.url3).then((res)=>{
console.log(res.data);
boat.css({"bottom":" 68%","left":"52%"})
$('.brand-position0').children("img").attr("src",this.ColorLogo[0])
})
})
}































},
play() {
this.flag = true;
const audio = document.getElementById('music');
audio.play();
},

back() {
this.$router.go(-1);
this.$destroy()
},
setClass(index) {
if (index === 0) {
return "brand-position0"
}
else if (index === 1) {
return "brand-position1"
}
else if (index === 2) {
return "brand-position3"
}
else if (index === 3) {
return "brand-position4"
}
else if (index === 4) {
return "brand-position6"
}
}
}
}



